const videoBlock = document.querySelector('.video-block');

if (videoBlock){
  const video = document.querySelector('.video-block__video');
  const playButton = document.querySelector('.video-block__btn_play');

  playButton.addEventListener("click", ()=>{
    videoBlock.classList.add('video_played');
    playButton.classList.add('btn-play_played');
    video.controls = true;
    video.play();
  })

  video.onpause = function (){
    videoBlock.classList.remove('video_played');
    video.controls = false;
    playButton.classList.remove('btn-play_played');
  }
}
