const btnsWorks = document.getElementsByClassName('works-filter__button')

Array.from(btnsWorks).forEach(function (btn){
  btn.addEventListener("click", function(e){
    e.preventDefault();
    if (this.querySelector('input').id === 'all'){
      Array.from(btnsWorks).forEach(function (btn){
        btn.classList.remove('works-filter__button_pressed');
        btn.querySelector('input').checked = false;
      })
      this.classList.add('works-filter__button_pressed');
      this.querySelector('input').checked = true;
    }
    else{
      btnAll = document.getElementById('works-btn_first');
      // btnAll = document.getElementsByClassName('works-filter__button')[0];
      btnAll.classList.remove('works-filter__button_pressed');
      btnAll.querySelector('input').checked = false;

      this.classList.toggle('works-filter__button_pressed');
      this.querySelector('input').checked ^= true;

      if (!document.querySelector('.works-filter__button_pressed')){
        btnAll.querySelector('input').checked = true;
        btnAll.classList.add('works-filter__button_pressed');
      }
    }
  })
})

