const itemAccord = document.querySelectorAll('.accordion__item');

Array.from(itemAccord).forEach(function (item){
  btn = item.querySelector('button');
  btn.addEventListener('click', function(e){
    item.style.setProperty('--accordion-time', `300/1000s`);
    item.classList.toggle('is-open');

    if (item.classList.contains('is-open')){
      control = item.querySelector('.accordion__btn');;
      content = item.querySelector('.accordion__body');;

      control.setAttribute('aria-expanded', true);
      content.setAttribute('aria-hidden', false);
      content.style.maxHeight = content.scrollHeight + 'px';
    }
    else{
      control = item.querySelector('.accordion__btn');;
      content = item.querySelector('.accordion__body');;

      control.setAttribute('aria-expanded', false);
      content.setAttribute('aria-hidden', true);
      content.style.maxHeight = null;
    }
  })
})

